.nav-container {
  padding: 0;
  margin: 0;
  border: 1px solid var(--main-green-color);

}
.nav {
  display: flex;
  flex-direction: row;
  padding: 0 15px;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 1.3rem 1rem;
  list-style: none;
  background-color: var(--main-green-color);
  border: none;

}

.nav-links a {
  text-decoration: none;
  color: var(--main-gray-color);
  font-weight: bold;
  font-size: 1.2rem;
}



.productlist-container {
  box-sizing:border-box;
  padding:0.5rem;
  display: grid;
  grid-template-columns: repeat( auto-fit, minmax(250px, 1fr) );
  grid-auto-rows: 100px; 

}

.product-card {
  max-width: 250;
  background-color: var(--secondary-gray-color);
  margin: 0.5rem;
  text-align: center;
  color: white;
}


.layout-container {
    width: 100%;
    height: 100%;
    background-color:var(--main-gray-color);
}

.layout-parent {
    width: 100vw;
    height: 95vh;
    overflow-y:hidden; 
    padding-top: 10px; 
    box-sizing: border-box;
}
.btn-container {
    display: flex;
    justify-content: space-evenly;
}

.layout-btn {
    padding: 1rem;
    width: 50%;
    margin:0;
    border: none;
    background-color: yellowgreen;
}

.layout-bord {
    font-weight:bold;
    font-size:1.5rem;
    color:rebeccapurple;
    display: flex;
    background-color: #A5FFD6;
    margin: 0;
    height: 100%; 
    border: 1px solid var(--main-gray-color);
    align-items: center;
    justify-content: center;
}

.bord-has-ticket {
  background-color: red;
}

.orderForm-parent {}

.orderForm-items {}

.table-number-title {
  font-size: 1.5rem;
  color: white;
  font-weight: bold;
  margin: 30px;
  width: auto;
}

.table-name {
  background-color: transparent;
  border: none;
  color: white;
  font-weight: bold;
  font-size: 1.5rem;

}


.table-num-holder {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.table-num-holder>input {
  background-color: transparent;
  border: none;
  color: white;
  font-weight: bold;
  font-size: 1.5rem;
}

.table-num-holder>input:focus {
  outline: 0;
  outline-color: var(--main-green-color);
}

.items-in-ticket {
  margin-left: 50px;
  justify-content: space-around;
  display: flex;
  align-items: center;
  background-color: var(--secondary-gray-color);
  padding: 2rem;
  width: 75vw;

}

.items-in-ticket>* {
  margin: 0px 10px;
  color: white;
  font-size: 1.3rem;

}

.items-in-ticket>input {
  background-color: transparent;
  border: none;
  color: white;
  font-weight: bold;
  width: 10%;
  text-align: center;
}

.items-in-ticket>input:focus {
  outline: 0;
  outline-color: var(--main-green-color);
}

.send-order-btn {
  padding: 15px;
  font-size: 1.1rem;
  font-weight:bold;
  color: white;
  border: 2px solid var(--main-green-color);
  background-color: var(--main-green-color);
}

.send-order-btn-holder {
    padding-right:10%;
    display:flex; 
    justify-content: flex-end;
    margin-top:1.5rem;
    margin-bottom: 3rem;
}

.ticket-parent {
 margin:1rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-auto-rows: 150px;
  grid-gap: 1rem;
}

.ticket-parent > * {
    background-color: var(--secondary-gray-color);
}

.allTickets-ids {
  padding: 2rem;
  font-size: 3rem;
  color: white;
  font-weight: bold;
  max-width: 250;
  text-align:center;
}

.ticket-links {
  text-decoration: none;
}

.ticket-table {
    padding: 1rem;
    margin: 0 auto;
    background-color: var(--secondary-gray-color);
    color: white;
    text-align: center;
    font-size: 1.5rem;
    margin: 1rem 2rem;
}

.ticket-tr, tr {
    height: 50px;
    width: 800px;
    font-size: 1rem;
}

.ticket-tr, th {
    font-weight: bolder;
    font-size: 2rem;
    height: 50px;
    width: 800px;
    font-size: 2rem;
}
.ticket-footer {
font-size: 1.5rem;
}
tr:nth-child(even) {background-color: var(--main-gray-color);}
.login-form {
  margin: 1rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: 40px;
  grid-gap: 1rem;
  grid-auto-flow: dense;
}

#login-form {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 250px;
}

.login-form>input {
  color: black;
  background-color: #FCF9FF;
  border-radius: 10px;
  padding: 0.8rem;
  font-weight: bold;
  text-align: center;
  border: 4px solid var(--secondary-gray-color);
  width: 250px;
}

.login-form>input:focus {
  outline:0;
  border: 3px solid var(--main-green-color);
  
}

.login-form>input::-webkit-input-placeholder {
  color: var(--secondary-gray-color);
}

.login-form>input::-moz-placeholder {
  color: var(--secondary-gray-color);
}

.login-form>input:-ms-input-placeholder {
  color: var(--secondary-gray-color);
}

.login-form>input::-ms-input-placeholder {
  color: var(--secondary-gray-color);
}

.login-form>input::placeholder {
  color: var(--secondary-gray-color);
}

.submitButton {
  margin-top: 20px;
  padding: 10px;
  font-size: 1.1rem;
  font-weight: bold;
  color: white;
  border: 2px solid var(--main-green-color);
  background-color: var(--main-green-color);
  border-radius:5px;
}

.form-holder {
  display:flex;
  flex-direction:column;
  justify-content: center; 
  align-items:center;
}
.calculateTime {
  margin-top: 15px;
  font-size: 1.5rem;
}
:root {
        --main-gray-color: #454d66;
        --secondary-gray-color: #565e76;
        --main-green-color: #58b368; 
      }

body {
  background-color: #454d66;
  background-color: var( --main-gray-color);
  font-family: 'Montserrat', sans-serif;
}

button:focus {outline:0;}

.logo-container{
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
  align-items: center;
  background-color: #58b368;
  background-color: var(--main-green-color);
  border: 1px solid #58b368;
  border: 1px solid var(--main-green-color);
  margin-bottom: -1px;
}
.logo {
  padding: 0;
}

.logo a {
  text-decoration: none;
  color: white;
}

.logout-btn {
  padding: 10px 15px;
  font-size: 1.1rem;
  font-weight:bold; 
  color: white;
  height: 80%;
  border-radius: 5px;
  background-color: red;
}

.username {
  color: white;
}
